import axios from "axios";
import { TRAINING_APP_URL } from "../config/app.config";
import { returnErrorToText } from "../utils/functions";
import { TrainingImplementationStatus } from "./training-offers";

export interface DistrictListTrainingsListInterface {
  id: number;
  school_code: number | null;
  school_name: string | null;
  total: number | null;
  training_id: string;
  title: string;
  description: string;
  start_date: string;
  end_date: string;
  intervation_id: number;
  intervantion_area: string;
  sub_area_id: number;
  subarea_name: string;
  topic_id: number;
  topic_name: string;
  development_partner_id: string;
  development_partner_name: string;
  implementation_status: TrainingImplementationStatus;
  is_approved: 1 | 0;
}

export interface GetTrainingsToBeApprovedListInterface {
  id: number;
  training_id: string;
  title: string;
  description: string;
  start_date: string;
  end_date: string;
  intervation_id: number;
  intervantion_area: string;
  sub_area_id: number;
  subarea_name: string;
  topic_id: number;
  topic_name: string;
  development_partner_id: string;
  development_partner_name: string;
  implementation_status: TrainingImplementationStatus;
}

// ************* Functions -------------------------------------------------------------------------------

// Get to be trained on district level
export const GetTrainingsFromSelectedDistrict = async (
  district_code: string,
  setResponse: (
    loading: boolean,
    res: {
      type: "success" | "error";
      msg: string;
      data: DistrictListTrainingsListInterface[] | null;
    } | null
  ) => void
) => {
  setResponse(true, null);
  try {
    const res = await axios.get<DistrictListTrainingsListInterface[]>(
      `${TRAINING_APP_URL}/district/training/${district_code}`
    );
    console.log("Trainings list: ", res.data);
    if (res) {
      setResponse(false, {
        data: res.data,
        type: "success",
        msg: "Data loaded successfully",
      });
    }
  } catch (error: any) {
    setResponse(false, {
      data: null,
      type: "error",
      msg: returnErrorToText(error),
    });
    console.log("Err: ", { ...error });
  }
};

// Get to be trained on district level
