import React, { Component } from "react";
import { AiOutlineUndo } from "react-icons/ai";
import { BsCheckCircleFill, BsShieldFillCheck } from "react-icons/bs";
import { FaChalkboardTeacher, FaUsers } from "react-icons/fa";
import { HiOutlineSelector } from "react-icons/hi";
import { MdOutlineGppGood } from "react-icons/md";
import { RiSearchLine } from "react-icons/ri";
import { connect } from "react-redux";
import { Auth, Basic, DistrictList, FC_GetDistrictList } from "../../actions";
import {
  ApproveTrainingTraineesRequest,
  DistrictListTrainingsListInterface,
  GetTrainingSchoolsTotalAssigned,
  GetTrainingsFromSelectedDistrict,
  GetTrainingsToBeApprovedListInterface,
} from "../../actions/training_approval.action";
import Button from "../../components/Buttons/Button";
import Loading from "../../components/Loading/Loading";
import Modal, { ModalSize } from "../../components/Modal/Modal";
import { PopUpContainer } from "../../components/PopUpContainer/PopUpContainer";
import { StoreState } from "../../reducers";
import { commaFy, DATE, search } from "../../utils/functions";
import TraineesList from "./components/TraineesList";
import { TrainingsList } from "./components/TrainingsList";

export interface SelectedDistrictInterface {
  district_code: string;
  district_name: string;
  province_code: string;
}

interface DDETrainingApprovalProps {
  basic: Basic;
  auth: Auth;
}
interface DDETrainingApprovalState {
  loading: boolean;
  selected_district: SelectedDistrictInterface | null;
  districts_list: SelectedDistrictInterface[] | null;
  error: string;
  chooseTrainingProvider: boolean;
  trainings_list: DistrictListTrainingsListInterface[] | null;
  selected_training: GetTrainingsToBeApprovedListInterface | null;
  selected_training_totals:
    | { total: number; trainee_request_id: number }[]
    | null;
  search_data: string;
  selected_training_request: {
    request_id: string;
    school_code: string;
    school_name: string;
    total: number;
    is_approved: 1 | 0;
  } | null;
  selected_status: 1 | 0 | null;
  approve_training_request: {
    request_id: string;
    school_code: string;
    school_name: string;
    total: number;
    is_approved: 1 | 0;
  } | null;
  success: string;
}

class _DDETrainingApproval extends Component<
  DDETrainingApprovalProps,
  DDETrainingApprovalState
> {
  constructor(props: DDETrainingApprovalProps) {
    super(props);

    this.state = {
      loading: false,
      error: "",
      districts_list: null,
      selected_district: this.props.auth.district,
      // this.props.auth.district !== null
      //   ? {
      //       district_code: this.props.auth.district.district_code,
      //       district_name: this.props.auth.district.district_name,
      //       province_code: this.props.auth.district.province_code,
      //     }
      //   : null,
      chooseTrainingProvider: false,
      trainings_list: null,
      selected_training: null,
      selected_training_totals: null,
      search_data: "",
      selected_training_request: null,
      selected_status: null,
      approve_training_request: null,
      success: "",
    };
  }
  LoadTotalTrainingAssignedTrainees = (training_id: string) => {
    this.setState({ loading: true });
    GetTrainingSchoolsTotalAssigned(
      training_id,
      (
        loading: boolean,
        res: {
          type: "success" | "error";
          msg: string;
          data: { total: number; trainee_request_id: number }[] | null;
        } | null
      ) => {
        this.setState({ loading: loading });
        if (res !== null && res.type === "success" && res.data !== null) {
          this.setState({ loading: false, selected_training_totals: res.data });
        }
        if (res !== null && res.type === "error") {
          this.setState({
            loading: false,
            selected_training_totals: [],
            error: res.msg,
          });
        }
      }
    );
  };
  GetDistrictsList = () => {
    this.setState({ loading: true });
    FC_GetDistrictList((status: boolean, data: DistrictList[], msg: string) => {
      if (status === true) {
        this.setState({
          loading: false,
          districts_list: data.map((item) => ({
            district_code: item.district_code,
            district_name: item.district_name,
            province_code: "",
          })),
          error: "",
        });
      }
    });
  };
  FC_GetTrainingsListToBeApproved = () => {
    this.state.selected_district !== null &&
      GetTrainingsFromSelectedDistrict(
        this.state.selected_district.district_code,
        (
          loading: boolean,
          res: {
            type: "success" | "error";
            msg: string;
            data: DistrictListTrainingsListInterface[] | null;
          } | null
        ) => {
          this.setState({ loading: loading });
          if (res !== null && res.type === "success" && res.data !== null) {
            this.setState({
              trainings_list: res.data,
              loading: false,
              chooseTrainingProvider: true,
            });
          }
          if (res !== null && res.type === "error") {
            this.setState({
              trainings_list: [],
              loading: false,
              error: res.msg,
            });
          }
        }
      );
  };
  // GetTrainingsList
  FC_GetTrainingsList = (): GetTrainingsToBeApprovedListInterface[] => {
    if (this.state.trainings_list !== null) {
      var response: GetTrainingsToBeApprovedListInterface[] = [];
      for (const item of this.state.trainings_list) {
        if (
          response.find(
            (itm) => itm.training_id.toString() === item.training_id.toString()
          ) === undefined
        ) {
          response = [
            ...response,
            {
              description: item.description,
              development_partner_id: item.development_partner_id,
              development_partner_name: item.development_partner_name,
              end_date: item.end_date,
              id: item.id,
              implementation_status: item.implementation_status,
              intervantion_area: item.intervantion_area,
              intervation_id: item.intervation_id,
              start_date: item.start_date,
              sub_area_id: item.sub_area_id,
              subarea_name: item.subarea_name,
              title: item.title,
              topic_id: item.topic_id,
              topic_name: item.topic_name,
              training_id: item.training_id,
            },
          ];
        }
      }
      return response;
    }
    return [];
  };
  FC_GetTrainingSchools = (): {
    request_id: string;
    school_code: string;
    school_name: string;
    total: number;
    is_approved: 1 | 0;
  }[] => {
    if (
      this.state.selected_training === null ||
      this.state.trainings_list === null
    ) {
      return [];
    } else {
      var response: {
        request_id: string;
        school_code: string;
        school_name: string;
        total: number;
        is_approved: 1 | 0;
      }[] = [];
      if (this.state.selected_training !== null) {
        for (const item of this.state.trainings_list.filter(
          (itm) =>
            this.state.selected_training !== null &&
            itm.training_id === this.state.selected_training.training_id
        )) {
          response.push({
            request_id: item.id.toString(),
            school_code:
              item.school_code === null ? "" : item.school_code.toString(),
            school_name: item.school_name === null ? "" : item.school_name,
            total: item.total === null ? 0 : item.total,
            is_approved: item.is_approved,
          });
        }
      }
      return response;
    }
  };
  GetTotalRequiredTrainees = (training_id: string): number => {
    if (
      this.state.trainings_list === null ||
      this.state.trainings_list.length === 0
    ) {
      return 0;
    }
    var total: number = 0;
    for (const item of this.state.trainings_list.filter(
      (itm) => itm.training_id.toString() === training_id.toString()
    )) {
      if (item.total !== null) {
        total += item.total;
      }
    }
    return total;
  };
  GetTotalRequiredByRequestId = (training_request_id: string): number => {
    if (
      this.state.trainings_list === null ||
      this.state.trainings_list.length === 0
    ) {
      return 0;
    }
    var total: number = 0;
    for (const item of this.state.trainings_list.filter(
      (itm) => itm.id.toString() === training_request_id.toString()
    )) {
      if (item.total !== null) {
        total += item.total;
      }
    }
    return total;
  };
  // Counting statuses
  GetTotalSchools = (): number => {
    return this.FC_GetTrainingSchools().length;
  };
  GetTotalSchoolsApproved = (): number => {
    if (this.state.selected_training_totals !== null) {
      var total = 0;
      for (const item of this.FC_GetTrainingSchools()) {
        const test = this.state.selected_training_totals.find(
          (itm) =>
            itm.trainee_request_id.toString() === item.request_id.toString() &&
            item.is_approved === 1
        );
        if (test !== undefined) {
          total += 1;
        }
      }
      return total;
    }
    return 0;
  };
  GetTotalAssignedByRequestId = (request_id: string): number => {
    if (this.state.selected_training_totals !== null) {
      const test = this.state.selected_training_totals.find(
        (itm) => itm.trainee_request_id.toString() === request_id.toString()
      );
      if (test !== undefined) {
        return test.total;
      }
    }
    return 0;
  };
  GetTotalByRequestId = (request_id: string): number => {
    if (this.state.trainings_list !== null) {
      const test = this.state.trainings_list.find(
        (itm) => itm.id.toString() === request_id.toString()
      );
      if (test !== undefined) {
        return test.total === null ? 0 : test.total;
      }
    }
    return 0;
  };
  GetTotalApprovedTrainees = (): number => {
    if (this.state.selected_training_totals !== null) {
      var total = 0;
      for (const item of this.FC_GetTrainingSchools()) {
        const test = this.state.selected_training_totals.find(
          (itm) =>
            itm.trainee_request_id.toString() === item.request_id.toString() &&
            item.is_approved === 1
        );
        if (test !== undefined) {
          total += test.total;
        }
      }
      return total;
    }
    return 0;
  };
  // Approve training

  FC_ApproveTrainingRequest = (training_request: {
    request_id: string;
    school_code: string;
    school_name: string;
    total: number;
    is_approved: 1 | 0;
  }) => {
    this.setState({ approve_training_request: null, loading: true });
    ApproveTrainingTraineesRequest(
      {
        is_approved: training_request.is_approved === 1 ? true : false,
        training_request_id: training_request.request_id,
      },
      (
        loading: boolean,
        res: {
          type: "success" | "error";
          msg: string;
        } | null
      ) => {
        this.setState({ loading: loading });
        if (res !== null && res.type === "success") {
          if (this.state.trainings_list !== null) {
            var response: DistrictListTrainingsListInterface[] = [];
            for (const item of this.state.trainings_list) {
              if (
                item.id.toString() === training_request.request_id.toString()
              ) {
                response.push({
                  description: item.description,
                  development_partner_id: item.development_partner_id,
                  development_partner_name: item.development_partner_name,
                  end_date: item.end_date,
                  id: item.id,
                  implementation_status: item.implementation_status,
                  intervantion_area: item.intervantion_area,
                  intervation_id: item.intervation_id,
                  is_approved: item.is_approved === 1 ? 0 : 1,
                  school_code: item.school_code,
                  school_name: item.school_name,
                  start_date: item.start_date,
                  sub_area_id: item.sub_area_id,
                  subarea_name: item.subarea_name,
                  title: item.title,
                  topic_id: item.topic_id,
                  topic_name: item.topic_name,
                  total: item.total,
                  training_id: item.training_id,
                });
              } else {
                response.push(item);
              }
            }
            this.setState({
              selected_status:
                response.filter((itm) => itm.is_approved === 0).length === 0
                  ? 1
                  : 0,
              trainings_list: response,
            });
          }
          this.setState({
            success: res.msg,
            error: "",
            loading: false,
          });
        }
        if (res !== null && res.type === "error") {
          this.setState({ success: "", error: res.msg, loading: false });
        }
      }
    );
  };

  componentDidMount(): void {
    if (this.state.selected_district === null) {
      //   Loading all districts list
      this.GetDistrictsList();
    }
    if (this.state.trainings_list === null) {
      this.FC_GetTrainingsListToBeApproved();
    }
  }
  render() {
    return (
      <div className="container mx-auto p-3 relative h-full">
        <div>
          <div className="bg-white rounded p-2">
            <div className="flex flex-col lg:flex-row lg:justify-between gap-2 w-full">
              <div className="flex flex-row items-center gap-2 w-full lg:w-1/2">
                <div>
                  <div className="h-14 w-14 rounded-md bg-accent-400 text-accent-900 flex items-center justify-center">
                    <MdOutlineGppGood className="text-5xl" />
                  </div>
                </div>
                <div className="flex flex-col">
                  <span className="text-xl font-bold">
                    Training participants approval
                  </span>
                  <span className="text-sm text-gray-500">
                    View list of selected participants for the training
                  </span>
                </div>
              </div>
              {/* Select DP */}
              <div className="w-full lg:w-1/2">
                <div
                  onClick={() =>
                    this.state.loading === false &&
                    this.setState({
                      chooseTrainingProvider:
                        !this.state.chooseTrainingProvider,
                    })
                  }
                  className={`${
                    this.state.selected_training !== null
                      ? "bg-accent-400 border-accent-500"
                      : "bg-gray-100 border-white"
                  } hover:bg-accent-400 border  hover:border-accent-500 w-full pl-3 py-1 pr-1 rounded flex flex-row items-center justify-between gap-20 group cursor-pointer animate__animated animate__fadeIn`}
                >
                  {this.state.selected_training !== null ? (
                    <div className="flex flex-row items-center gap-2">
                      {/* <div>
                        <BsCheckCircleFill className="text-3xl" />
                      </div> */}
                      <div className="flex flex-col">
                        <span className="text-black text-sm">
                          Selected training
                        </span>
                        <div className="font-bold text-yellow-700">
                          {this.state.selected_training.title}
                        </div>
                      </div>
                    </div>
                  ) : (
                    <div className="text-lg font-normal pl-2 text-gray-500">
                      No training selected!
                    </div>
                  )}
                  <div className="">
                    <div className="bg-white group-hover:bg-accent-900 group-hover:text-white rounded p-1 flex items-center justify-center">
                      <HiOutlineSelector className="text-4xl text-accent-900 group-hover:text-white" />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        {/* Body */}
        <div className="h-full">
          {/* Error here */}
          {this.state.error !== "" && (
            <div
              onClick={() => this.setState({ error: "", success: "" })}
              className="bg-red-50 text-red-700 font-bold px-3 py-2 my-3"
            >
              <div>Error occurred!</div>
              <div className="font-normal">{this.state.error}</div>
            </div>
          )}
          {/* Success */}
          {this.state.success !== "" && (
            <div
              onClick={() => this.setState({ success: "", error: "" })}
              className="bg-green-50 text-green-700 font-bold px-3 py-2 my-3 animate__animated animate__zoomIn rounded-md border-2 border-white"
            >
              <div>Action succeeded!</div>
              <div className="font-normal">{this.state.success}</div>
            </div>
          )}
          {/* Filter and search training */}
          {this.state.loading === false ? (
            this.state.selected_training === null ? (
              <div>
                <div className="flex flex-col items-center justify-center bg-white rounded-md p-6 mt-3">
                  <div className="mb-2">
                    <FaChalkboardTeacher className="text-6xl text-gray-300 animate__animated animate__shakeX" />
                  </div>
                  <div className="text-xl font-bold">
                    Select training for approval
                  </div>
                  <div className="text-sm">
                    No training selected to be approved, click to select
                    training
                  </div>
                  <Button
                    onClick={() =>
                      this.state.loading === false &&
                      this.setState({
                        chooseTrainingProvider:
                          !this.state.chooseTrainingProvider,
                      })
                    }
                    className="mt-3"
                  >
                    Select training
                  </Button>
                </div>
              </div>
            ) : (
              <div className="bg-white rounded my-2 p-2 h-full animate__animated animate__fadeIn">
                {/* <div className="">
                  Total schools, total approved, total trainees requested, total
                  trainees submitted, vancant trainees
                </div> */}
                {/* Statistical summary */}
                <div className="flex flex-row items-center justify-between gap-2 mb-4">
                  <div className="font-bold text-lg pl-2">Trainees summary</div>
                  <div className="flex flex-row items-center justify-end gap-2">
                    <div className="bg-gray-100 rounded-full px-3 w-max">
                      Start date:{" "}
                      {DATE(this.state.selected_training.start_date)}
                    </div>
                    <div className="bg-gray-100 rounded-full px-3 w-max">
                      Ending date: {DATE(this.state.selected_training.end_date)}
                    </div>
                  </div>
                </div>
                <div className="mb-6 flex flex-col lg:flex-row items-center justify-between gap-2">
                  <div className="flex flex-row items-center justify-between gap-2 h-full">
                    <div className="rounded-md bg-gray-100 p-1 px-5 flex flex-col justify-center">
                      <div className="text-sm">Total schools</div>
                      <div className="font-bold text-xl">
                        {commaFy(this.GetTotalSchools())}
                      </div>
                    </div>
                    <div
                      className="rounded-md bg-green-50 text-green-700 p-1 px-5 flex flex-col justify-center"
                      style={{ borderColor: "#afd9cc" }}
                    >
                      <div className="text-sm">Total approved schools</div>
                      <div className="font-bold text-xl">
                        {commaFy(this.GetTotalSchoolsApproved())}
                      </div>
                    </div>
                    <div className="rounded-md bg-red-50 text-red-700 p-1 px-5 flex flex-col justify-center">
                      <div className="text-sm">Remaining schools</div>
                      <div className="font-bold text-xl">
                        {commaFy(
                          this.GetTotalSchools() -
                            this.GetTotalSchoolsApproved()
                        )}
                      </div>
                    </div>
                  </div>
                  <div className="flex flex-row items-center justify-between gap-2">
                    <div className="rounded-md bg-gray-100 p-1 px-5 flex flex-col justify-center">
                      <div className="text-sm">Total required trainees</div>
                      <div className="font-bold text-xl">
                        {commaFy(
                          this.GetTotalRequiredTrainees(
                            this.state.selected_training.training_id
                          )
                        )}
                      </div>
                    </div>
                    <div
                      className="rounded-md bg-green-50 text-green-700 p-1 px-5 flex flex-col justify-center"
                      style={{ borderColor: "#afd9cc" }}
                    >
                      <div className="text-sm">Total approved trainees</div>
                      <div className="font-bold text-xl">
                        {commaFy(this.GetTotalApprovedTrainees())}
                      </div>
                    </div>
                    <div className="rounded-md bg-red-50 text-red-700 p-1 px-5 flex flex-col justify-center">
                      <div className="text-sm">Not approved trainees</div>
                      <div className="font-bold text-xl">
                        {commaFy(
                          this.GetTotalRequiredTrainees(
                            this.state.selected_training.training_id
                          ) - this.GetTotalApprovedTrainees()
                        )}
                      </div>
                    </div>
                  </div>
                </div>
                {/* Search data */}
                <div className="flex flex-row items-center gap-2 w-full">
                  <select
                    value={
                      this.state.selected_status === null
                        ? ""
                        : this.state.selected_status.toString()
                    }
                    onChange={(e) =>
                      this.setState({
                        selected_status:
                          e.target.value === "1"
                            ? 1
                            : e.target.value === "0"
                            ? 0
                            : null,
                      })
                    }
                    className="px-3 py-2 rounded bg-primary-100 -mt-3 border border-primary-700 text-primary-800"
                  >
                    <option value="0">
                      Not approved schools (
                      {
                        this.FC_GetTrainingSchools().filter(
                          (itm) => itm.is_approved === 0
                        ).length
                      }
                      )
                    </option>
                    <option value="1">
                      Approved schools (
                      {
                        this.FC_GetTrainingSchools().filter(
                          (itm) => itm.is_approved === 1
                        ).length
                      }
                      )
                    </option>
                    <option value="">
                      All schools ({this.FC_GetTrainingSchools().length})
                    </option>
                  </select>
                  <div className="relative w-full mb-3">
                    <div className="absolute px-3 py-2">
                      <RiSearchLine className="text-2xl text-gray-600" />
                    </div>
                    <input
                      type="search"
                      className="font-normal bg-gray-100 w-full px-3 pl-12 py-2 rounded border-2 border-gray-100"
                      placeholder="Search..."
                      value={this.state.search_data}
                      onChange={(e) =>
                        this.setState({ search_data: e.target.value })
                      }
                    />
                  </div>
                </div>
                <div className="text-xl font-light">
                  <table className="w-full text-left text-sm">
                    <thead>
                      <tr className="border-b border-gray-300">
                        <th className="px-3 py-3 border border-gray-300">#</th>
                        <th className="px-3 py-3 border border-gray-300">
                          School code
                        </th>
                        <th className="px-3 py-3 border border-gray-300">
                          School name
                        </th>
                        <th className="px-3 py-3 border border-gray-300">
                          Total required
                        </th>
                        <th className="px-3 py-3 border border-gray-300">
                          Total assigned
                        </th>
                        <th className="px-3 py-3 border border-gray-300">
                          Status
                        </th>
                        <th className="px-3 py-3 border border-gray-300">
                          Action
                        </th>
                      </tr>
                    </thead>
                    <tbody>
                      {(
                        search(
                          this.FC_GetTrainingSchools().filter(
                            (itm) =>
                              this.state.selected_status === null ||
                              itm.is_approved === this.state.selected_status
                          ),
                          this.state.search_data
                        ) as {
                          request_id: string;
                          school_code: string;
                          school_name: string;
                          total: number;
                        }[]
                      ).length === 0 ? (
                        <tr>
                          <td
                            colSpan={6}
                            className="py-3 text-center text-xl font-light"
                          >
                            No result found!
                          </td>
                        </tr>
                      ) : (
                        (
                          search(
                            this.FC_GetTrainingSchools().filter(
                              (itm) =>
                                this.state.selected_status === null ||
                                itm.is_approved === this.state.selected_status
                            ),
                            this.state.search_data
                          ) as {
                            request_id: string;
                            school_code: string;
                            school_name: string;
                            total: number;
                            is_approved: 1 | 0;
                          }[]
                        ).map((item, i) => (
                          <tr key={i + 1}>
                            <td className="px-3 py-2 border border-gray-300">
                              {i + 1}
                            </td>
                            <td className="px-3 py-2 border border-gray-300">
                              {item.school_code === ""
                                ? this.state.selected_district?.district_code
                                : item.school_code}
                            </td>
                            <td className="px-3 py-2 border border-gray-300">
                              {item.school_name === ""
                                ? this.state.selected_district?.district_name
                                : item.school_name}
                            </td>
                            <td className="px-3 py-2 border border-gray-300">
                              {commaFy(item.total)}
                            </td>
                            <td className="px-3 py-2 border border-gray-300">
                              {commaFy(
                                this.GetTotalAssignedByRequestId(
                                  item.request_id
                                )
                              )}
                            </td>
                            <td className="px-3 py-2 border border-gray-300">
                              {item.is_approved.toString() === "1" ? (
                                <div className="w-max bg-green-100 rounded-full text-sm px-3 py-1 text-green-600 font-normal">
                                  Approved
                                </div>
                              ) : (
                                <div className="w-max bg-accent-400 rounded-full text-sm px-3 py-1 text-accent-900 font-normal">
                                  Not approved
                                </div>
                              )}
                            </td>
                            <td className="px-1 py-1 border border-gray-300 w-10">
                              <div className="flex flex-row items-center justify-end gap-2 w-max">
                                <div
                                  onClick={() =>
                                    this.setState({
                                      selected_training_request: item,
                                    })
                                  }
                                  className="flex flex-row items-center justify-center gap-3 bg-gray-100 text-gray-700 hover:bg-gray-400 hover:text-white rounded p-2 pr-3 w-max cursor-pointer font-bold group"
                                >
                                  <div>
                                    <FaUsers className="text-2xl text-gray-400 group-hover:text-white" />
                                  </div>
                                  <span>Trainees</span>
                                </div>
                                {item.is_approved !== 1 ? (
                                  <div
                                    onClick={() => {
                                      this.setState({
                                        approve_training_request: {
                                          request_id: item.request_id,
                                          school_code: item.school_code,
                                          school_name: item.school_name,
                                          total: item.total,
                                          is_approved: 1,
                                        },
                                      });
                                    }}
                                    className="flex flex-row items-center justify-center gap-3 bg-green-100 text-green-600 hover:bg-green-600 hover:text-white rounded p-2 pr-3 w-max cursor-pointer font-bold"
                                  >
                                    <div>
                                      <BsCheckCircleFill className="text-2xl" />
                                    </div>
                                    <span>Approve</span>
                                  </div>
                                ) : (
                                  <div
                                    onClick={() => {
                                      this.setState({
                                        approve_training_request: {
                                          request_id: item.request_id,
                                          school_code: item.school_code,
                                          school_name: item.school_name,
                                          total: item.total,
                                          is_approved: 0,
                                        },
                                      });
                                    }}
                                    className="flex flex-row items-center justify-center gap-1 bg-accent-400 text-accent-900 hover:bg-accent-900 hover:text-white rounded p-2 pr-3 w-max cursor-pointer font-bold"
                                  >
                                    <div>
                                      <AiOutlineUndo className="text-2xl" />
                                    </div>
                                    <span>Undo approval</span>
                                  </div>
                                )}
                              </div>
                            </td>
                          </tr>
                        ))
                      )}
                    </tbody>
                  </table>
                </div>
              </div>
            )
          ) : (
            <div className="flex flex-col items-center justify-center p-6 bg-white rounded my-4">
              <Loading />
              <div className="text-center font-light text-lg">
                Loading, please wait...
              </div>
            </div>
          )}
        </div>
        {this.state.chooseTrainingProvider === true &&
          this.state.trainings_list !== null && (
            <PopUpContainer className="p-3 px-4">
              <TrainingsList
                onGoBack={() =>
                  this.setState({ chooseTrainingProvider: false })
                }
                trainings_list={this.FC_GetTrainingsList()}
                selected_training={this.state.selected_training}
                onSelected={(data: GetTrainingsToBeApprovedListInterface) => {
                  this.setState({
                    chooseTrainingProvider: false,
                    selected_training: data,
                  });
                  this.LoadTotalTrainingAssignedTrainees(data.training_id);
                }}
                GetTotalRequiredTrainees={this.GetTotalRequiredTrainees}
                GetTotalRequiredByRequestId={this.GetTotalRequiredByRequestId}
              />
            </PopUpContainer>
          )}
        {this.state.selected_training !== null &&
          this.state.selected_training_request !== null && (
            <PopUpContainer className="p-3 px-4">
              <TraineesList
                training_id={this.state.selected_training.training_id}
                selected_training_request={this.state.selected_training_request}
                onGoBack={() =>
                  this.setState({ selected_training_request: null })
                }
                GetTotalRequiredByRequestId={this.GetTotalAssignedByRequestId}
                onSubmitApproval={(training_request_id: string) => {
                  this.state.selected_training_request !== null &&
                    this.setState({
                      approve_training_request: {
                        is_approved: 1,
                        request_id:
                          this.state.selected_training_request.request_id,
                        school_code:
                          this.state.selected_training_request.school_code,
                        school_name:
                          this.state.selected_training_request.school_name,
                        total: this.state.selected_training_request.total,
                      },
                      selected_training_request: null,
                    });
                }}
              />
            </PopUpContainer>
          )}

        {this.state.approve_training_request !== null && (
          <Modal
            backDrop={true}
            close={() => this.setState({ approve_training_request: null })}
            backDropClose={true}
            displayClose={false}
            widthSizeClass={ModalSize.large}
            padding={{
              title: undefined,
              body: true,
              footer: undefined,
            }}
          >
            <div>
              <div
                className={`text-2xl font-bold pb-3 mb-3 ${
                  this.state.approve_training_request.is_approved === 1
                    ? "text-green-700"
                    : "text-accent-900"
                } flex flex-row items-center gap-3`}
              >
                <div>
                  {this.state.approve_training_request.is_approved === 1 ? (
                    <BsShieldFillCheck className="text-3xl" />
                  ) : (
                    <AiOutlineUndo className="text-3xl" />
                  )}
                </div>
                <span>
                  {this.state.approve_training_request.is_approved === 1
                    ? "Training trainees approval"
                    : "Undo training approval"}
                </span>
              </div>
              <div className="">
                <div>School name</div>
                <div className="font-bold text-lg">
                  {this.state.approve_training_request.school_code} -{" "}
                  {this.state.approve_training_request.school_name}
                </div>
              </div>
              <div className="rounded-md my-3 flex flex-row items-center justify-between gap-3">
                <div className="rounded-md p-1 flex flex-row items-center gap-3 border border-accent-600 w-full bg-accent-40 animate__animated animate__fadeIn">
                  <div>
                    <div className="h-14 w-14 rounded-md bg-accent-400 flex items-center justify-center text-accent-900">
                      <FaUsers className="text-4xl" />
                    </div>
                  </div>
                  <div className="flex flex-col ">
                    <span className="">Total required trainees</span>
                    <span className="font-bold text-2xl text-accent-900">
                      {commaFy(this.state.approve_training_request.total)}
                    </span>
                  </div>
                </div>
                <div
                  className="rounded-md p-1 flex flex-row items-center gap-3 border w-full bg-accent-40 group animate__animated animate__fadeIn"
                  style={{ borderColor: "#afd9cc" }}
                >
                  <div>
                    <div className="h-14 w-14 rounded-md bg-green-100 flex items-center justify-center text-green-600">
                      <FaChalkboardTeacher className="text-4xl" />
                    </div>
                  </div>
                  <div className="flex flex-col w-full">
                    <span className="">Total Assigned trainees</span>
                    <div className="flex flex-row items-center justify-between gap-2 w-full">
                      <span className="font-bold text-2xl text-green-600">
                        {commaFy(
                          this.GetTotalAssignedByRequestId(
                            this.state.approve_training_request.request_id
                          )
                        )}
                      </span>
                      <div
                        onClick={() => {
                          this.state.approve_training_request !== null &&
                            this.setState({
                              selected_training_request: {
                                is_approved:
                                  this.state.approve_training_request
                                    .is_approved === 1
                                    ? 0
                                    : 1,
                                request_id:
                                  this.state.approve_training_request
                                    .request_id,
                                school_code:
                                  this.state.approve_training_request
                                    .school_code,
                                school_name:
                                  this.state.approve_training_request
                                    .school_name,
                                total:
                                  this.state.approve_training_request.total,
                              },
                              approve_training_request: null,
                            });
                        }}
                        className="cursor-pointer px-2 py-1 rounded text-sm bg-gray-100 font-bold w-max float-right group-hover:bg-accent-400 group-hover:text-accent-900"
                      >
                        View trainees
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="font-light">
                <div>Are you sure do you want to confirm?</div>
              </div>
              <div className="flex flex-row items-center justify-end gap-2">
                <Button
                  onClick={() =>
                    this.setState({ approve_training_request: null })
                  }
                  theme="default"
                >
                  Cancel
                </Button>
                <Button
                  onClick={() => {
                    this.state.approve_training_request !== null &&
                      this.FC_ApproveTrainingRequest(
                        this.state.approve_training_request
                      );
                  }}
                  theme={
                    this.state.approve_training_request.is_approved === 1
                      ? "success"
                      : "accent"
                  }
                  className="animate__animated animate__zoomIn"
                >
                  {this.state.approve_training_request.is_approved === 1
                    ? "Approve training trainees"
                    : "Undo training approval"}
                </Button>
              </div>
            </div>
          </Modal>
        )}
      </div>
    );
  }
}

const mapStateToProps = ({
  basic,
  auth,
}: StoreState): { basic: Basic; auth: Auth } => {
  return { basic: basic, auth: auth };
};

export const DDETrainingApproval = connect(
  mapStateToProps,
  {}
)(_DDETrainingApproval);
