import React, { Component } from "react";
import { BiReset } from "react-icons/bi";
import { BsArrowLeft, BsArrowRight, BsCalendar2Range } from "react-icons/bs";
import { FaTimes } from "react-icons/fa";
import { RiSearchLine } from "react-icons/ri";
import { GetTrainingsToBeApprovedListInterface } from "../../../actions/training_approval.action";
import SelectCustom from "../../../components/SelectCustom/SelectCustom";
import { commaFy, DATE, search } from "../../../utils/functions";

interface TrainingsListProps {
  trainings_list: GetTrainingsToBeApprovedListInterface[];
  selected_training: GetTrainingsToBeApprovedListInterface | null;
  onSelected: (data: GetTrainingsToBeApprovedListInterface) => void;
  onGoBack: () => void;
  GetTotalRequiredTrainees: (training_id: string) => number;
  GetTotalRequiredByRequestId: (training_request_id: string) => number;
}
interface TrainingsListState {
  search_data: string;
  loading: boolean;
  openSelectTrainingProvider: boolean;
  selected_training_provider_id: {
    training_provider_id: string;
    training_provider_name: string;
  } | null;
}

export class TrainingsList extends Component<
  TrainingsListProps,
  TrainingsListState
> {
  constructor(props: TrainingsListProps) {
    super(props);

    this.state = {
      loading: false,
      search_data: "",
      openSelectTrainingProvider: false,
      selected_training_provider_id: null,
    };
  }
  ReturnFilterTrainings = (): GetTrainingsToBeApprovedListInterface[] => {
    var data = this.props.trainings_list;
    if (this.state.selected_training_provider_id !== null) {
      data = data.filter(
        (itm) =>
          this.state.selected_training_provider_id !== null &&
          itm.development_partner_id.toString() ===
            this.state.selected_training_provider_id.training_provider_id.toString()
      );
    }
    return search(
      data,
      this.state.search_data
    ) as GetTrainingsToBeApprovedListInterface[];
  };
  GetTotalRequired = () => {
    var total: number = 0;
    for (const item of this.props.trainings_list) {
      total += this.props.GetTotalRequiredTrainees(item.training_id);
    }
    return total;
  };
  GetTrainingProviders = () => {
    const response: {
      training_provider_id: string;
      training_provider_name: string;
    }[] = [];
    for (const item of this.props.trainings_list) {
      if (
        response.find(
          (itm) =>
            itm.training_provider_id.toString() ===
            item.development_partner_id.toString()
        ) === undefined
      ) {
        response.push({
          training_provider_id: item.development_partner_id,
          training_provider_name: item.development_partner_name,
        });
      }
    }
    return response;
  };
  render() {
    return (
      <div>
        <div className="flex flex-col lg:flex-row items-start lg:items-center gap-3 lg:justify-between px-2 py-2 pb-4">
          <div className="flex flex-row items-center gap-3 w-full">
            <div>
              <div
                onClick={this.props.onGoBack}
                className="p-1 rounded-full bg-accent-400 h-12 w-12 flex items-center justify-center cursor-pointer"
              >
                <BsArrowLeft className="text-3xl text-accent-900 animate__animated animate__zoomIn" />
              </div>
            </div>
            <div className="truncate">
              <div className="text-xl font-bold">Trainings list</div>
              <div className="text-gray-500">
                Choose or select a training from the list
              </div>
            </div>
          </div>
          <div className="flex flex-row items-center justify-end gap-2">
            <div className="flex flex-col items-center justify-center bg-primary-100 p-2 px-3 rounded">
              <div className="truncate text-sm text-primary-700">
                Total required trainees
              </div>
              <div className="font-bold text-xl text-primary-700">
                {commaFy(this.GetTotalRequired())}
              </div>
            </div>
            {/* <div className="flex flex-col items-center justify-center bg-gray-100 p-2 rounded">
              <div className="truncate text-sm text-gray-700">
                Total required
              </div>
              <div className="font-bold text-xl text-gray-700">
                {this.GetTotalRequired()}
              </div>
            </div>
            <div className="flex flex-col items-center justify-center bg-green-50 p-1 px-3 rounded">
              <div className="truncate text-4xl text-green-600">
                <HiCheckCircle />
              </div>
              <div className="text-sm text-green-700 truncate">
                {"Validation Done"}
              </div>
            </div> */}
          </div>
        </div>
        {/* Search */}
        <div className="flex flex-row items-center gap-2 justify-between w-full">
          <div className="relative w-full rounded bg-primary-100 -mt-3 text-primary-700 cursor-pointer hover:bg-primary-100">
            <div
              onClick={() =>
                this.setState({
                  openSelectTrainingProvider:
                    !this.state.openSelectTrainingProvider,
                })
              }
              className="flex flex-row items-center justify-between gap-2 w-full py-2 px-3"
            >
              <div className="font-bold">
                {this.state.selected_training_provider_id !== null ? (
                  this.state.selected_training_provider_id
                    .training_provider_name
                ) : (
                  <span className="text-primary-800">
                    Filter by training provider
                  </span>
                )}
              </div>
              <div>
                {this.state.openSelectTrainingProvider === false ? (
                  <BsArrowRight className="text-2xl" />
                ) : (
                  <FaTimes className="text-2xl text-yellow-700" />
                )}
              </div>
            </div>

            {this.state.openSelectTrainingProvider === true && (
              <div className="absolute pt-2 animate__animated animate__zoomIn animate__faster text-black">
                {/* <div>Select training provider</div> */}
                <SelectCustom
                  loading={this.state.loading}
                  id={"training_provider_id"}
                  title={"training_provider_name"}
                  close={() =>
                    this.setState({ openSelectTrainingProvider: false })
                  }
                  data={this.GetTrainingProviders()}
                  click={(data: {
                    training_provider_id: string;
                    training_provider_name: string;
                  }) => {
                    this.setState({
                      selected_training_provider_id: data,
                      openSelectTrainingProvider: false,
                    });
                  }}
                />
              </div>
            )}
          </div>
          {/* Reset */}
          {this.state.selected_training_provider_id !== null && (
            <div
              title="Reset"
              className="py-1 px-2 border border-accent-500 text-yellow-600 h-11 bg-accent-400 rounded-md -mt-3 hover:bg-accent-500 cursor-pointer flex items-center justify-center animate__animated animate__zoomIn animate__faster"
              onClick={() =>
                this.setState({ selected_training_provider_id: null })
              }
            >
              <BiReset className="text-3xl" />
            </div>
          )}
          <div className="relative w-full mb-3">
            <div className="absolute px-3 py-2">
              <RiSearchLine className="text-2xl text-gray-600" />
            </div>
            <input
              type="search"
              className="font-normal bg-gray-100 w-full px-3 pl-12 py-2 rounded border border-gray-100"
              placeholder="Search..."
              value={this.state.search_data}
              onChange={(e) => this.setState({ search_data: e.target.value })}
            />
          </div>
        </div>
        {/* Trainings list */}
        {this.props.trainings_list.length === 0 ? (
          <div>
            <div className="flex flex-col items-center justify-center bg-gray-100 rounded-md p-6">
              <div className="mb-2">
                <BsCalendar2Range className="text-6xl text-gray-300 animate__animated animate__shakeX" />
              </div>
              <div className="text-xl font-bold">No trainings available</div>
              <div className="text-sm">
                No trainings available for the selected district to be approved
              </div>
            </div>
          </div>
        ) : (
          <div>
            <table className="w-full text-sm text-left">
              <thead>
                <tr>
                  <th className="px-3 py-2 border border-gray-300">#</th>
                  <th className="px-3 py-2 border border-gray-300">
                    Training title
                  </th>
                  <th className="px-3 py-2 border border-gray-300">
                    Training provider
                  </th>
                  <th className="px-3 py-2 border border-gray-300">Topic</th>
                  <th className="px-3 py-2 border border-gray-300">
                    Start date
                  </th>
                  <th className="px-3 py-2 border border-gray-300">End date</th>
                  {/* <th className="px-3 py-2 border border-gray-300">Status</th> */}
                  <th className="px-3 py-2 border border-gray-300">
                    Total required
                  </th>
                  {/* <th className="px-3 py-2 border border-gray-300">
                    Total provided
                  </th> */}
                  <th className="px-3 py-2 border border-gray-300"></th>
                </tr>
              </thead>
              <tbody>
                {this.ReturnFilterTrainings().map((item, i) => (
                  <tr
                    key={i + 1}
                    className={`cursor-pointer hover:bg-yellow-50 hover:text-accent-900 group`}
                    onClick={() =>
                      this.props.selected_training !== null &&
                      this.props.selected_training.id === item.id
                        ? this.props.onGoBack()
                        : this.props.onSelected(item)
                    }
                  >
                    <td className="px-3 py-2 border border-gray-300">
                      {i + 1}
                    </td>
                    <td className="px-3 py-2 border border-gray-300">
                      {item.title}
                    </td>
                    <td className="px-3 py-2 border border-gray-300">
                      {item.development_partner_name}
                    </td>
                    <td className="px-3 py-2 border border-gray-300">
                      {item.topic_name}
                    </td>
                    <td className="px-3 py-2 border border-gray-300">
                      {DATE(item.start_date)}
                    </td>
                    <td className="px-3 py-2 border border-gray-300">
                      {DATE(item.end_date)}
                    </td>
                    {/* <td className="px-3 py-2 border border-gray-300">
                      {item.implementation_status}
                    </td> */}
                    <td className="px-3 py-2 border border-gray-300">
                      {commaFy(
                        this.props.GetTotalRequiredTrainees(
                          item.training_id.toString()
                        )
                      )}
                    </td>
                    {/* <td className="px-3 py-2 border border-gray-300">{10}</td> */}
                    <td className="px-1 py-1 border border-gray-300 w-10">
                      {this.props.selected_training !== null &&
                      this.props.selected_training.id === item.id ? (
                        <div className="bg-white px-2 py-1 border wounded-md cursor-not-allowed rounded-md text-gray-500">
                          Selected
                        </div>
                      ) : (
                        <div className="px-2 py-1 rounded bg-accent-400 text-accent-900 text-sm flex flex-row items-center justify-center gap-2 w-max float-right group-hover:bg-accent-900 group-hover:text-white font-bold">
                          <div>Select</div>
                          <div>
                            <BsArrowRight className="text-2xl" />
                          </div>
                        </div>
                      )}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}
      </div>
    );
  }
}
