import React, { Component } from "react";
import { BsArrowLeft, BsCheckCircleFill } from "react-icons/bs";
import { MdClose } from "react-icons/md";
import { RiSearchLine } from "react-icons/ri";
import {
  GetSchoolTrainingParticipants,
  TrainingParticipants,
} from "../../../actions/training-offers";
import Loading from "../../../components/Loading/Loading";
import { search } from "../../../utils/functions";
import { removeDuplicateParticipantsSubjects } from "../../TrainingOffers/School/Details/AddParticipants";

interface TraineesListProps {
  training_id: string;
  selected_training_request: {
    request_id: string;
    school_code: string;
    school_name: string;
    total: number;
    is_approved: 1 | 0;
  };
  onGoBack: () => void;
  GetTotalRequiredByRequestId: (training_request_id: string) => number;
  onSubmitApproval: (training_request_id: string) => void;
}
interface TraineesListState {
  loading: boolean;
  trainees: TrainingParticipants[] | null;
  error: string;
  search_data: string;
  view_subjects: TrainingParticipants | null;
}

export class TraineesList extends Component<
  TraineesListProps,
  TraineesListState
> {
  constructor(props: TraineesListProps) {
    super(props);

    this.state = {
      loading: false,
      trainees: null,
      error: "",
      search_data: "",
      view_subjects: null,
    };
  }
  LoadParticipants = () => {
    this.setState({ loading: true });
    GetSchoolTrainingParticipants(
      this.props.training_id,
      this.props.selected_training_request.school_code,
      (
        loading: boolean,
        res: {
          type: "success" | "error";
          msg: string;
          data: TrainingParticipants[] | null;
        } | null
      ) => {
        this.setState({ loading: loading });
        if (res !== null && res.type === "success") {
          this.setState({ trainees: res.data, loading: false });
        }
        if (res !== null && res.type === "error") {
          this.setState({ trainees: [], loading: false, error: res.msg });
        }
      }
    );
  };
  componentDidMount = () => {
    if (this.state.trainees === null) {
      this.LoadParticipants();
    }
  };
  render() {
    return (
      <div>
        <div className="text-xl font-bold">Trainings List</div>
        <div className="flex flex-row items-center justify-between gap-2">
          <div className="flex flex-row items-center gap-2">
            <div className="flex flex-row items-center justify-between gap-3">
              <div>
                <div
                  onClick={this.props.onGoBack}
                  className="h-11 w-11 cursor-pointer bg-accent-400 text-accent-900 hover:bg-accent-500 flex items-center justify-center rounded-full"
                >
                  <BsArrowLeft className="text-2xl" />
                </div>
              </div>
              <div className="bg-primary-100 rounded-md flex flex-col w-max px-3 py-1 text-primary-800 my-2">
                <span className="text-sm">Selected school</span>
                <span className="font-bold text-base">
                  {this.props.selected_training_request.school_code} -{" "}
                  {this.props.selected_training_request.school_name}
                </span>
              </div>
            </div>
          </div>
          <div className="flex flex-row items-center justify-end gap-2">
            <div className=" px-4 py-0 text-right">
              <div className="text-sm">Total trainees</div>
              <div className="text-lg -mt-1 font-bold">
                {this.state.trainees === null
                  ? "..."
                  : this.state.trainees.length}{" "}
                of {this.props.selected_training_request.total}
              </div>
            </div>
            <div>
              {this.props.selected_training_request.is_approved !== 1 && (
                <div
                  onClick={() => {
                    this.props.onSubmitApproval(
                      this.props.selected_training_request.request_id
                    );
                  }}
                  className="flex flex-row items-center justify-center gap-3 bg-green-100 text-green-700 hover:bg-green-700 hover:text-white rounded px-3 py-2 w-max cursor-pointer font-bold"
                >
                  <div>
                    <BsCheckCircleFill className="text-2xl" />
                  </div>
                  <span>Approve</span>
                </div>
              )}
            </div>
          </div>
        </div>
        {/* Table here */}
        {/* error */}
        {this.state.error !== "" && (
          <div
            onClick={() => this.setState({ error: "" })}
            className="bg-red-100 text-red-700 px-3 py-2 rounded cursor-pointer flex flex-row items-center justify-between gap-2 my-3 animate__animated animate__shakeX"
          >
            <div className="flex flex-col">
              <span className="text-lg font-bold">Error occurred:</span>
              <span>{this.state.error}</span>
            </div>
            <div>
              <MdClose className="text-2xl" />
            </div>
          </div>
        )}
        <div className="relative w-full mt-3">
          <div className="absolute px-3 py-2">
            <RiSearchLine className="text-2xl text-gray-600" />
          </div>
          <input
            type="search"
            className="font-normal bg-gray-100 w-full px-3 pl-12 py-2 rounded"
            placeholder="Search..."
            value={this.state.search_data}
            onChange={(e) => this.setState({ search_data: e.target.value })}
          />
        </div>
        <div className="w-full overflow-x-auto">
          {this.state.trainees === null ? (
            <div className="flex flex-col items-center justify-center p-6 bg-gray-100 rounded my-4">
              <Loading />
              <div className="text-center font-light text-lg">
                Loading, please wait...
              </div>
            </div>
          ) : this.state.trainees.length === 0 ? (
            <div>
              <div className="text-center p-5 bg-gray-100 rounded-md mt-3 flex flex-col items-center justify-center w-full">
                <div className="text-xl font-bold">No trainees found!</div>
                <div className="text-sm text-gray-500">
                  No trainees assigned to the selected training from the school{" "}
                  {this.props.selected_training_request.school_name}
                </div>
                <div className="mt-4">
                  <div
                    className="bg-white hover:bg-gray-300 border border-gray-300 px-3 py-2 rounded w-max cursor-pointer italic"
                    onClick={this.props.onGoBack}
                  >
                    Go back to schools list
                  </div>
                </div>
              </div>
            </div>
          ) : (
            <div className="mt-3 w-full overflow-x-auto">
              <table className="min-w-full text-left text-sm animate__animated animate__fadeIn">
                <thead>
                  <tr className="text-primary-800 truncate">
                    <th className="px-2 py-2 border border-gray-300 text-center">
                      #
                    </th>
                    <th className="px-2 py-2 border border-gray-300">
                      Full name
                    </th>
                    <th className="px-2 py-2 border border-gray-300">
                      National ID
                    </th>
                    <th className="px-2 py-2 border border-gray-300">
                      Teaching level
                    </th>
                    <th className="px-2 py-2 border border-gray-300">
                      Qualification
                    </th>
                    <th className="px-2 py-2 border border-gray-300">
                      Position
                    </th>
                    <th className="px-2 py-2 border border-gray-300 text-right">
                      Subjects taught
                    </th>
                  </tr>
                </thead>
                <tbody>
                  {(
                    search(
                      this.state.trainees,
                      this.state.search_data
                    ) as TrainingParticipants[]
                  ).length === 0 ? (
                    <tr>
                      <td
                        colSpan={8}
                        className="p-6 text-lg font-light text-center"
                      >
                        <div className="animate__animated animate__shakeX text-center">
                          No result found!
                        </div>
                      </td>
                    </tr>
                  ) : (
                    (
                      search(
                        this.state.trainees,
                        this.state.search_data
                      ) as TrainingParticipants[]
                    ).map((item, i) => (
                      <tr key={i + 1} className="group">
                        <td className="px-2 py-2 border border-gray-300 text-center">
                          {i + 1}
                        </td>
                        <td className="px-2 py-2 border border-gray-300">
                          {item.first_name} {item.last_name}
                        </td>
                        <td className="px-2 py-2 border border-gray-300">
                          {item.nid}
                        </td>
                        <td className="px-2 py-2 border border-gray-300">
                          {item.teaching_level}
                        </td>
                        <td className="px-2 py-2 border border-gray-300">
                          {item.qualification_name}
                        </td>
                        <td className="px-2 py-2 border border-gray-300">
                          {item.position_name}
                        </td>
                        <td className="px-1 py-1 border border-gray-300">
                          {item.subjects.length > 0 ? (
                            <div
                              onClick={() =>
                                this.setState({ view_subjects: item })
                              }
                              className="bg-gray-100 px-2 py-2 rounded cursor-pointer hover:text-primary-800 text-sm w-max hover:bg-primary-100 float-right"
                            >
                              View subjects (
                              {
                                removeDuplicateParticipantsSubjects(
                                  item.subjects
                                ).length
                              }
                              )
                            </div>
                          ) : (
                            <div className="px-3">No subjects</div>
                          )}
                          {this.state.view_subjects !== null &&
                            this.state.view_subjects.nid === item.nid && (
                              <div
                                className="bg-white border rounded-md shadow-lg absolute right-5 animate__animated animate__zoomIn animate__faster"
                                style={{ zIndex: 9 }}
                              >
                                <div className="flex flex-row items-center justify-between gap-2 p-2 px-3">
                                  <span className="font-bold text-lg">
                                    Subjects taught
                                  </span>
                                  <div>
                                    <div
                                      onClick={() =>
                                        this.setState({
                                          view_subjects: null,
                                        })
                                      }
                                      className="h-8 w-8 rounded-full bg-red-100 text-red-700 cursor-pointer hover:bg-red-200 flex items-center justify-center"
                                    >
                                      <MdClose className="text-2xl" />
                                    </div>
                                  </div>
                                </div>
                                {/* List */}
                                <div className="p-2">
                                  <table className="w-full text-sm text-left">
                                    <thead>
                                      <td className="px-3 py-2 border border-gray-300 font-bold">
                                        #
                                      </td>
                                      <td className="px-3 py-2 border border-gray-300 font-bold">
                                        Course code
                                      </td>
                                      <td className="px-3 py-2 border border-gray-300 font-bold">
                                        Course name
                                      </td>
                                    </thead>
                                    <tbody>
                                      {removeDuplicateParticipantsSubjects(
                                        this.state.view_subjects.subjects
                                      ).map((course, i) => (
                                        <tr key={i + 1} className={``}>
                                          <td className="px-3 py-2 border border-gray-300">
                                            {i + 1}
                                          </td>
                                          <td className="px-3 py-2 border border-gray-300">
                                            {course.course_code}
                                          </td>
                                          <td className="px-3 py-2 border border-gray-300">
                                            {course.course_name}
                                          </td>
                                        </tr>
                                      ))}
                                    </tbody>
                                  </table>
                                </div>
                              </div>
                            )}
                        </td>
                      </tr>
                    ))
                  )}
                </tbody>
              </table>
            </div>
          )}
        </div>
      </div>
    );
  }
}

export default TraineesList;
